import React from "react";
import { useState, useEffect } from "react";
import location from "../images/location.png";
import cloud from "../images/cloud.png";
import rain from "../images/rain.png";
import clear from "../images/clear.png";
import drizzle from "../images/drizzle.png"
import thunderstrom from "../images/thunderstrom.png"
import snow from "../images/snow.png"

function LocationWeather() {

    const date = new Date().toLocaleDateString("en-IN", {
        month: 'short',
        day: 'numeric',
        weekday: 'long'
    });

    const [temperature, settemperature] = useState()
    const [cityname, setcityname] = useState()
    const [cloudicon, setcloudicon] = useState()
    const [icon, seticon] = useState()




    useEffect(() => {
        navigator.geolocation.getCurrentPosition((position) => {
            const { latitude, longitude } = position.coords;
            // console.log(`The latitude is : ${latitude} and longitude is : ${longitude} `)

            const fetchApione = async () => {
                const url = `https://api.openweathermap.org/data/2.5/weather?lat=${latitude}&lon=${longitude}&appid=cead5d445fd157513f3d8d470398335b&units=metric`;
                const response = await fetch(url);
                const resjson = await response.json();
                console.log(resjson);
                settemperature(resjson.main);
                setcityname(resjson.name);
                setcloudicon(resjson.weather[0].main);
                const iconcopy = resjson.weather[0].main;

                if (iconcopy === 'Clouds') {
                    seticon(cloud)
                }
                else if (iconcopy === 'Rain') {
                    seticon(rain)
                }
                else if (iconcopy === 'Thunderstorm') {
                    seticon(thunderstrom)
                }
                else if (iconcopy === 'Drizzle') {
                    seticon(drizzle)
                }
                else if (iconcopy === 'Snow') {
                    seticon(snow)
                }
                else {
                    seticon(clear)
                }
            }
            fetchApione();
        })



    }, [])



    return (
        <>

            {!temperature ? (
                <div className="container-location">

                    <div className="location-name">
                        <img src={location} alt={location} className="location-icon" />
                        Not Available
                    </div>
                    <div className="date">
                        <div className="today">Today</div>
                        <div className="dateiconcopy">{date}</div>
                    </div>

                    <div className="location-temperature">
                        <div className="temperature">
                            ! Data Not Found
                        </div>
                    </div>
                    <div className="btn"><button>Forecast ➡</button></div>
                </div>

            ) : (
                <div className="container-location">

                    <div className="location-name">
                        <img src={location} alt={location} className="location-icon" />
                        {cityname}, India
                    </div>
                    <div className="date">
                        <div className="today">Today</div>
                        <div className="dateiconcopy">{date}</div>
                    </div>

                    <div className="location-temperature">
                        <div className="cloud-icon">
                            <img src={icon} alt={icon} className="weather-icon" />
                            <div className="description">
                                {cloudicon}
                            </div>

                        </div>

                        <div className="temperature">
                            {temperature.temp.toFixed(0)}°c
                            <div className="min-max">
                                <div className="max-temp"> Feels like: {temperature.feels_like.toFixed(0)}°c</div>
                                <div className="min-temp">Humidity: {temperature.humidity}%  </div>
                            </div>
                        </div>
                    </div>


                    <div className="btn"><button>Forecast ➡</button></div>
                </div>
            )

            }

        </>
    )
}

export default LocationWeather;